.digi-body .clock-day:before {
  content: var(--timer-day);
}
.digi-body .clock-hours:before {
  content: var(--timer-hours);
}
.digi-body .clock-minutes:before {
  content: var(--timer-minutes);
}
.digi-body .clock-seconds:before {
  content: var(--timer-seconds);
}

.digi-body {
  background: linear-gradient(45deg, #1870ed 0, #f18f88 100%);
  font-family: "Poppins", "sans-serif";
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
}

.digi-body .clock-container {
  margin-top: 30px;
  margin-bottom: 30px;
  background-color: #080808;
  border-radius: 5px;
  padding: 40px 10px;
  box-shadow: 1px 1px 5px rgba(255, 255, 255, 0.15), 0 15px 90px 30px rgba(0, 0, 0, 0.25);
  display: flex;
}
.digi-body .clock-col {
  text-align: center;
  margin-right: 20px;
  margin-left: 20px;
  min-width: 90px;
  position: relative;
}
.digi-body .clock-col:not(:last-child):before,
.digi-body .clock-col:not(:last-child):after {
  content: "";
  background-color: rgba(255, 255, 255, 0.3);
  height: 5px;
  width: 5px;
  border-radius: 50%;
  display: block;
  position: absolute;
  right: -42px;
}
.digi-body .clock-col:not(:last-child):before {
  top: 35%;
}
.digi-body .clock-col:not(:last-child):after {
  top: 50%;
}
.digi-body .clock-timer {
  color: #fff;
  font-size: 3.2rem;
  text-transform: uppercase;
  margin: 0;
}
.digi-body .clock-label {
  color: rgba(255, 255, 255, 0.35);
  text-transform: uppercase;
  font-size: 0.7rem;
  margin: 0;
}

